е, играет Гимн Российской Федерации, скандируют трибуны... Они не сравнимы ни с чем. К сожалению, в обычной жизни их очень трудно повторить. И только в 2013 году, когда мне вручали главную награду студенчества — «Гран-при» конкурса «Студент года», я вновь испытала те чувства и эмоции. Это очень здорово, что у нас в регионе есть конкурс, который собирает самых талантливых студентов нашего региона и позволяет каждому проявить себя и получить вознаграждение за свои труды. ")])])]),_c('div',{staticClass:"history-winners"},[_c('div',{staticClass:"history-winners--info"},[_c('img',{staticClass:"history-winners--info--photo",attrs:{"src":"/landing/images/history/author2.png","alt":"Победитель"}}),_c('span',{staticClass:"history-winners--info--name"},[_vm._v("Сергей Заика")]),_c('div',{staticClass:"history-winners--info--description"},[_vm._v(" Победитель «Гран-при» Конкурса 2014 года, руководитель профсоюзной организации Самарского университета. Также является руководителем смены «Управляй будущим» молодежного форума «iВолга», смены молодых управленцев. ")])]),_c('div',{staticClass:"history-winners--quote"},[_c('span',[_vm._v(" Вообще любой активист независимо от сферы: научная, общественная, творческая и любая другая — это тот человек, который очень много и усердно работает. Это человек, который тратит свое личное время, чем-то жертвует для реализации чего-то нового и крутого. Перед такими людьми рано или поздно обязательно возникнет вопрос: правильно ли я поступаю, когда трачу так много времени на свою деятельность? И этот конкурс является очень громким положительным ответом на этот вопрос — «Да, ты поступаешь правильно и всё делаешь верно!». Когда ты получаешь награду, то понимаешь, что ты являешься примером для многих людей, потому что очень многие хотят ее получить, но сегодня это смог сделать именно ты. И в следующий раз уже другие участники будут равняться на тебя. С этой точки зрения, для меня Конкурс стал социальным лифтом, я по-другому стал смотреть на многие вещи. Если до Конкурса я через свою деятельность пытался раскрыть свой потенциал, то после победы все мои проекты были нацелены на раскрытие потенциала других ребят. ")])])]),_c('div',{staticClass:"history-winners"},[_c('div',{staticClass:"history-winners--info"},[_c('img',{staticClass:"history-winners--info--photo",attrs:{"src":"/landing/images/history/author3.png","alt":"Победитель"}}),_c('span',{staticClass:"history-winners--info--name"},[_vm._v("Сергей Пушняк")]),_c('div',{staticClass:"history-winners--info--description"},[_vm._v(" Победитель Конкурса в 2017 года в номинации «Студенческий лидера вуза», а также обладатель «Гран-при», Сергей Пушняк – руководитель Самарского регионального штаба Самарского регионального отделения Молодежной общероссийской общественной организации «Российские студенческие отряды». ")])]),_c('div',{staticClass:"history-winners--quote"},[_c('span',[_vm._v(" «Студент года» — это хорошая конкурсная площадка, которая объединяет студентов и руководство образовательных организаций, общественные, творческие и спортивные объединения региона. Победа на «Студенте года» помогает мне и сейчас в работе со студентами, образовательными организациями, партнерами с которыми я взаимодействовал во время студенчества — эти знакомства и контакты я использую и сейчас в развитие движения студенческих отрядов. Для молодежи Самарской области «Студент года» — возможность привнести в свою деятельность новые знания и внедрить их на практике. «Студентам года» доверяют. Очень ценно, что со стороны Губернатора и Правительства Самарской области, профильных министерств есть доверие к молодежи нашего региона. ")])])])])])])}]

